* {
  margin: 0;
}

:root {
  --body-color: rgb(194, 16, 238);
  --text-color: #ebfdff;
}

body {
  background: var(--body-color);
  color: var(--text-color);
  padding-bottom: 2rem;
}

button {
  cursor: pointer;
  height: 2rem;
  background-color: var(--body-color);
  color: var(--text-color);
  font-weight: bold;
  border: none;
  border-radius: 4px;
}

button:hover {
  background-color: rgb(141, 0, 176);
}

.heading {
  place-content: center;
  display: flex;
  margin-top: 2rem;
}

.Home {
  display: flex;
  justify-content: center;
  gap: 1rem;
  padding: 1rem;
}

.card {
  background-color: var(--text-color);
  padding: 1rem;
  width: 100%;
}

.scanned-code {
  color: black;
  display: grid;
  gap: 0.4rem;
}

.scanned-code input {
  height: 1.3rem;
  padding: 5px 7px;
}

/* text-generator */
.text-generator .content {
  display: grid;
}

.text-generator .content input {
  height: 1.8rem;
  padding: 5px 7px;
}

.text-generator .content button {
  margin-top: 1rem;
}

.text-generator .QRcode {
  display: flex;
  justify-content: center;
  height: 20rem;
  align-items: center;
}

.text-generator .QRcode img {
  padding: 1rem;
  border: 2px solid black;
  border-radius: 10px;
  width: 10rem;
}

.text-generator .QRcode img.active {
  border: 3px solid var(--body-color);
}


/* scan-qrcode */
.scan-qrcode {
  display: grid;
  gap: 1rem;
}

.scan-qrcode button {
  width: 100%;
}

.scan-qrcode .scanner {
  width: 80%;
  margin: auto;
}

/* web-cam-qrcode */
.web-cam-qrcode {
  display: grid;
  gap: 1rem;
}

.web-cam-qrcode .title {
  color: black;
  margin: 0;
}

.web-cam-qrcode .web-cam {
  width: 100%;
  height: fit-content;
  min-height: 15rem;
  border: 1px solid rgba(0, 0, 0, 0.164);
  display: flex;
  align-items: center;
}

.web-cam-qrcode .scanner {
  width: 100%;
}

@media (max-width:905px) {
  .web-cam-qrcode .heading {
    font-size: 1rem;
  }

  .scanned-code h3 {
    font-size: 1rem;
  }
}

@media (max-width:796px) {
  .web-cam-qrcode .heading {
    font-size: 0.9rem;
  }

  .scanned-code h3 {
    font-size: 0.9rem;
  }
}

@media (max-width:710px) {
  .Home {
    display: grid;
    gap: 2rem;
  }

  .card {
    width: 25rem;
  }

  .web-cam-qrcode .title {
    font-size: 1.2rem;
  }

  .scanned-code h3 {
    font-size: 1.5rem;
  }
}

@media (max-width:455px) {
  .heading {
    font-size: 1.3rem;
  }

  .card {
    width: 20rem;
  }
}

@media (max-width:388px) {
  .heading {
    font-size: 1.2rem;
  }

  .card {
    width: 18rem;
  }

  .scanned-code h3 {
    font-size: 1.2rem;
  }
}

@media (max-width:330px) {
  .heading {
    font-size: 1.1rem;
  }

  .card {
    width: 15rem;
  }

  .Home {
    gap: 1.2rem;
  }

  .web-cam-qrcode .title {
    font-size: 1.1rem;
  }
}

/* Footer  */
footer{
  text-align: center;
  font-size: 12px;
}
footer span{
  cursor: pointer;
}